import React, { Component } from "react";

class SetPageScroll extends Component {
    constructor(props) {
      super(props);
      this.state = {
          pageName : this.props.pageName,
      }
    }

    componentDidMount() {
        if (sessionStorage.getItem(this.state.pageName) ) {
            window.scrollTo(window.scrollX, JSON.parse(sessionStorage.getItem(this.state.pageName)), true);
        }
        window.addEventListener('unload', this.saveScroll())
    }

    saveScroll = () => {
        sessionStorage.setItem(this.state.pageName, JSON.stringify(window.scrollY));
        console.log(sessionStorage.getItem(this.state.pageName));
    }

    componentWillUnmount() {
        window.removeEventListener('unload', this.saveScroll())
    }

    render() {
        return ( <></>);
    }
}
export default SetPageScroll;